@import "./_reset.scss";
body {
  background-image: linear-gradient(120deg, #f3f1f5 0%, #dae5f0 100%);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  min-height: 100vh;
}

.loader {
  width: 120px;
  height: 120px;
  border-radius: 40%;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.loader,
.loader:before,
.loader:after {
  animation: 1s infinite ease-in-out;
}
.loader:before,
.loader:after {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}
.loader-2:before,
.loader-2:after {
  content: "";
}
.loader-2:before {
  border: 1px solid rgb(100, 98, 98);
  top: -1px;
  left: -1px;
  opacity: 0;
  animation-name: loader2-1;
}

@keyframes loader2-1 {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.2);
    opacity: 0;
  }
  100% {
    transform: scale(1.3);
    opacity: 0;
  }
}

.loader-2:after {
  background-color: rgb(245, 165, 165);
  animation-name: loader2-2;
}

@keyframes loader2-2 {
  0% {
    transform: scale(0.7);
  }
  50% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(0.7);
  }
}
.movies {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(900px, 1fr));
  grid-gap: 1rem;
  padding-top: 0px;
  padding: 10px -20px;
  margin-bottom: 50px;
}

@media all and (max-width: 1024px) {
  .movies {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
    grid-gap: 1rem;
    padding-top: 20px;
    padding: 20px 50px;
  }
}
